<template>
  <v-container>
    <v-card>
      <v-card-text>
        <v-row>
          <v-col
            cols="4"
            sm="4"
            md="4"
          >
            <v-autocomplete
              v-model="channel"
              :items="channels"
              label="Выбрать источник"
              placeholder="(по названию или все)"
              @change="onChangeChannel(channel)"
            ></v-autocomplete>
          </v-col>
          <v-col
            cols="4"
          >
            <v-menu
              v-model="dateFromModal"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="fromDate"
                  label="На период с"
                  prepend-icon="mdi-calendar-blank-multiple"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker
                v-model="fromDate"
                @input="dateFromModal = false"
              />
            </v-menu>
          </v-col>
          <v-col
            cols="4"
          >
            <v-menu
              v-model="dateToModal"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="toDate"
                  label="По"
                  prepend-icon="mdi-calendar-blank-multiple"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker
                v-model="toDate"
                @input="dateToModal = false"
              />
            </v-menu>
          </v-col>
        </v-row>
        <v-row>
          <v-col
            cols="4"
            class="pb-0 pt-0"
          >
            <p class="col-title">Доступ</p>
          </v-col>
          <v-col
            cols="8"
            class="pb-0 pt-0"
          >
            <v-btn
              text
              small
              :color="dataSet.perm?'primary':'error'"
              @click="dataSet.perm=!dataSet.perm"
            >
              <span
                v-if="dataSet.perm"
              >Открыть</span>
              <span v-else>
                Закрыть
              </span>
            </v-btn>
          </v-col>
          <v-col
            cols="4"
            class="pb-0 pt-0"
          >
            <p class="col-title">Просмотр календаря</p>
          </v-col>
          <v-col
            cols="8"
            class="pb-0 pt-0"
          >
            <v-btn
              text
              small
              :color="dataSet.calendar?'primary':'error'"
              @click="dataSet.calendar=!dataSet.calendar"
            >
              <span
                v-if="dataSet.calendar"
              >Да</span>
              <span v-else>
                Нет
              </span>
            </v-btn>
          </v-col>
          <v-col
            cols="4"
            class="pb-0 pt-0"
          >
            <p class="col-title">Форма бронирования</p>
          </v-col>
          <v-col
            cols="8"
            class="pb-0 pt-0"
          >
            <v-select
              dense
              v-model="dataSet.bookingForm"
              :items="bookingForms"
            />
          </v-col>
          <v-col
            cols="4"
            class="pb-0 pt-0"
          >
            <p class="col-title">Разрешить статусы</p>
          </v-col>
          <v-col
            cols="8"
            class="pb-0 pt-0"
          >
            <v-select
              v-model="dataSet.statusPerm"
              dense
              :items="statusPerms"
            />
          </v-col>
          <v-col
            cols="4"
            class="pb-0 pt-0"
          >
            <p class="col-title">Лимит на продажу</p>
          </v-col>
          <v-col
            cols="8"
            class="pb-0 pt-0"
          >
            <v-select
              v-model="dataSet.saleLimit"
              dense
              :items="saleLimits"
            />
          </v-col>
          <v-col
            cols="4"
            class="pb-0 pt-0"
          >
            <p class="col-title">По номерам/По категория</p>
          </v-col>
          <v-col
            cols="8"
            class="pb-0 pt-0"
          >
            <v-select
              dense
              :items="roomTypes"
            />
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-actions>
        <v-btn
          color="primary"
          @click="save()"
        >
          Сохранить
        </v-btn>
        <v-btn
          color="primary"
        >
          Добавить блоки
        </v-btn>
        <v-btn
          color="primary"
        >
          Выход
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
  export default {
    name: 'SalesSettings',
    data () {
      return {
        fromDate: new Date().toISOString().substr(0, 10),
        dateFromModal: false,
        toDate: new Date().toISOString().substr(0, 10),
        dateToModal: false,
        dataSet: {
          id: 3,
          name: 'Go2trip',
          city: 'Алматы',
          datebeg: new Date(2019, 2, 12).toDateString(),
          commSize: '10%',
          blocks: 'Да',
          perm: false,
          value: 'go2trip',
          calendar: true,
          bookingForm: 'order',
          statusPerm: 'reserve',
          saleLimit: 'count',
        },
        channel: null,
        bookingForm: null,
        bookingForms: [
          {
            text: 'Заявка',
            value: 'order',
          },
          {
            text: 'Указать на шахматке',
            value: 'calendar',
          },
        ],
        statusPerms: [
          {
            text: 'Резерв',
            value: 'reserve',
          },
          {
            text: 'Бронирование',
            value: 'booking',
          },
        ],
        saleLimits: [
          {
            text: '%',
            value: 'perc',
          },
          {
            text: 'Количество',
            value: 'count',
          },
        ],
        roomTypes: [
          {
            text: 'Стандарт ',
            value: 'std',
          },
          {
            text: 'Полулюкс ',
            value: 'halfLux',
          },
          {
            text: 'Люкс',
            value: 'lux',
          },
        ],
      }
    },
    methods: {
      onChangeChannel (payload) {
        this.dataSet = this.channels[payload]
      },
      async save () {
        await this.$store.commit('SAVE_SALE_SETTINGS', this.dataSet)
        window.location.href = '#/sales-channels/list'
      },
    },
    computed: {
      channels: function () {
        const obj = []
        for (const channel in this.$store.state.salesChannel) {
          obj.push({
            text: this.$store.state.salesChannel[channel].name,
            value: channel,
            id: channel,
            calendar: this.$store.state.salesChannel[channel].calendar,
            bookingForm: this.$store.state.salesChannel[channel].blocks,
            statusPerm: this.$store.state.salesChannel[channel].statusPerm,
            saleLimit: this.$store.state.salesChannel[channel].saleLimit,
            perm: this.$store.state.salesChannel[channel].perm,
            blocks: this.$store.state.salesChannel[channel].blocks,
          })
        }
        return obj
      },
    },
  }
</script>
<style>
  .col-title {
    font-size: 18px !important;
    font-weight: 500 !important;
    color: orange !important;
    padding-bottom: 0 !important;
    padding-top: 0 !important;
  }
</style>
